<template>
<transition name="translate">
		<div class="rank-detail" @touchstart.stop.prevent @touchend.stop.prevent @touchmove.stop.prevent.self>
		<div class="rank-title">
			<div class="rank-intro">
				<div class="rank-intro-img">
					<img src="" alt="">
				</div>
				<div class="rank-intro-detail">
					<h3 class="rank-name">巅峰吧</h3>
					<p class="rank-day">第14回</p>
					<p class="rank-update">2013更新</p>
				</div>
			</div>
			<div class="rank-play-all">
				<div class="ran-play-all-btn"><a href="">播放全部</a></div>
			</div>
		</div>
		<div class="panel">
			<scroll class="rank-music-list">
			<h4>排行榜 公222首是</h4>
			<ul>
				<li class="list-item">
					<span>1</span>
					<h5>buwen</h5>
					<p>lalayuedui</p>
				</li>
			</ul>
		</scroll>>
		</div>
	</div>
</transition>

</template>

<script>
import Scroll from "components/base/Scroll"
export default{ 
	name:"rank-detail",
	components:{
		Scroll
	}

}

</script>

<style lang="less" type="text/less" scoped>
@import "~common/less/variable";
@import "~common/less/mixin";
.rank-detail{
	position: fixed;
	left: 0;
	z-index: 1001;
	top: 0;
	right: 0;
	bottom: 0;
	color:@color-text-l;
	background-color: @color-background-l;
	.rank-title{
		background-color:rgba(0,0,0,.6);
		padding:128px 32px 0;
		.rank-intro{

			.list(row,display);
			.rank-intro-img{
				width: 250px;
				height: 250px;
				margin-right:20px;
				img{
					width: 100%;
				}

			}
			.rank-intro-detail{

				flex:1;
				.list(column);
				.no-wrap;
				height:250px;
				justify-content: center;
				.rank-name{
					font-size: @font-size-large;
					line-height: 1.3;
				}
				.rank-day,.rank-update{
					font-size: @font-size-small;
					line-height: 2;
				}
			}


		}
		.rank-play-all{
			height: 168px;
			text-align: center;
			a{
				display:inline-block;
				line-height: 80px;
				width: 340px;
				text-align: center;
				background-color: @color-theme;
				color:@color-text-l;
				border-radius: 60px;

				&::before{
					content:"";
					height: 0;
					width:5px;
					display: inline-block;
					line-height: 80px;
					border:15px solid transparent;
					border-left-color:@color-text-l;
				}


			}
		}
	}
	.rank-music-list{

		color:@color-text;
		h4{
			padding:0 28px;
			font-size: @font-size-medium;
			line-height: 108px;
			color:#777;
		}
		.list-item{
			padding-left: 90px;
			position: relative;
			height: 124px; 
			.list(column);
			justify-content: center;
			line-height: 1.4;
			.no-wrap;
			span{
				position: absolute;
color:@color-danger;
				left: 0;
				top: 0;
				line-height: 108px;
				width: 90px;
				text-align:center;
			};
			h5{
				font-size: @font-size-medium-x;
				.no-wrap;
			}
			p{
				font-size: @font-size-small;
				.no-wrap;
		}}
	}
	.panel{
		overflow: hidden;
	}

}
	.translate-enter-active,.translate-fade-active{

			transition:all 1s
		}
		.translate-enter,.translate-leave-to{
			opacity:.6;
			transform: translateX(100%);
		}

</style>


