import Vue from 'vue'
import Router from 'vue-router'
import Welcome from 'components/welcome'

import Rank from 'components/rank'
import Recommend from 'components/recommend'
import Search from 'components/search'
import List from 'components/list'
import Singer from 'components/singer'

import ServerAgrement from 'components/tool/ServerAgrement'
import Uninstall from 'components/tool/Uninstall'

import fastclick from 'fastclick'
import VueLazyload from 'vue-lazyload'
import MusicDetail from "components/music-detail"
import RankDetail from "components/rank-detail"
// import Axios from 'axios'


fastclick.attach(document.body)

Vue.use(Router)
Vue.use(VueLazyload,{loading:require('common/img/lazy2.png'),preLoad:1})
  // {preLoad:1.3,
  //   error:require('common/img/lazy.png'),
  //   loading:require('common/img/lazy.png'),
  //   attempt:1}
    // )
// Vue.prototype.$http = Axios



export default new Router({
  linkActiveClass: 'active',
  routes: [{
    path: '/',
    redirect: '/welcome'
  },
  {
    path: '/recommend',
    component:Recommend,

  },
  {
    path: '/welcome',
    component: Welcome,
    children:[
      {
        path: 'server',
        component: ServerAgrement
      },
      {
        path: 'uninstall',
        component: Uninstall
      },

    ]

  },
  {
    path: '/recommend',
    component: Recommend,
  },

  {
    path: '/rank',
    component: Rank,
    children:[
    {
      path:"1",
      component:RankDetail
    }

    ]
  },
   {
    path: '/search',
    component: Search,
    children:[
    {
      path:"1",
      component:MusicDetail

    }
    ]
  },
  ]
})



