<template>
	<scroll class="search" @end="scroll"  ref="search" >
		<div class="search-wrap" >
			<div class="search-form"  >
				<div class="search-input">
					<i class="icon-sousuo" ></i>
					<input @keyup.enter="searching" placeholder="搜索歌曲，歌单，专辑" type="text" v-model="search" ref="input" @focus="changeFocus(true)" class="search-input-text" :class="{focus:onfocus}">
					<i class="icon-empty" v-show="search" @click="empty"></i>
				</div>
				<span class="delete" v-show="onfocus" @click="cancle">取消</span>
			</div>
			<div ref="searchform" class="list-panel">
					<keep-alive>

					<hot-search ref="lala" @copyHot="copyHot" v-if="!onfocus" :special="special" :hotkey="hotkey"></hot-search>
					<history @copy="copyHot" @delete="deleteHistory"  v-else-if="isHistory" :history="histories"></history>
					<music-list v-else :musicList="musicList" ref="myMusicList"></music-list  >
				</keep-alive>
				
			</div>
</div>
		
		<router-view></router-view>
	
	</scroll>
	
		
	
</template>

<script>
import HotSearch from "components/search/Hot-search"
import {ERR_OK} from 'api/config.js'
import {getHotKey,getSeachList} from "api/search"
import History from "components/search/History"
import MusicList from "components/music-list"
import {setIndex} from "common/js/array"
import {getMusicList} from "common/js/music"
import Scroll from "components/base/Scroll"

export default {
	name: 'search',
	components:{HotSearch,History,MusicList,Scroll},
	computed:{
		musicList(){
			return {isAll:this.isAll,zhida:this.zhida,list:this.list};
		}},
		mounted(){

		},
		destroyed(){
		},
		data(){
			return{
				on:true,
				search:"",
				test:{name:1},
				onfocus:false,
				special:{key:"",url:""},
				hotkey:[],
				histories:[],
				isHistory:true,
				p:1,
				isAll:false,
				zhida:{},
				list:[],
				touchStart:0,
				touchChange:0,
				touchon:true
			}
		},
		created(){
			this._getHotKey();

		},
		methods:{
			scroll(e){
			
					this._getMusicList();
					
				
			},
			copyHot(val){
				this.search=val;
				this.searching();

			},
			delete(){
				this.isAll=false;
				this.list=[];
				this.zhida={};
			},
			deleteHistory(index){
				this.histories.splice(index,1);
			},
			empty(){this.search=""},
			cancle(){
				this.changeFocus(false);
				this.isHistory=true;
				this.search="";
				this.delete();
			},
			_getMusicList(){

				getSeachList(this.search,this.p).then(res=>{
					if(res.code===ERR_OK){
						let {zhida,song}=res.data;

						song.list.forEach(item=>{

							this.list.push(getMusicList(item))

						})
						this.zhida=Object.assign({},this.zhida,zhida)
						this.isAll=(song.totalnum-(song.curnum*song.curpage))>0?false:true;

					}				
					this.p++;
					this.on=true;			
				});
			},
			searching(){

				if(this.search!=""){
					setIndex(this.histories,this.search,0)

					this.onfocus=true;
					this.List=[];
					this.delete();	
					this.page=1;		
					this.isHistory=false;

					this._getMusicList();
				}	
			},
			_getHotKey(){

				getHotKey().then((res=>{
					if(res.code===ERR_OK){
						let key=res.data.special_key,url=res.data.special_url;
						this.special=Object.assign({},this.special,{key,url})
						this.hotkey=res.data.hotkey.sort(()=>Math.random()-0.5).splice(0,6)
					}
				}))
			},
			changeFocus(val){
				this.onfocus=val;
			},
		}
	}
	</script>

	<style lang="less" type="text/less" scoped>
	@import '~common/less/variable';
	@import '~common/less/mixin';

	.search{
		// z-index: 1000;
		height: 100%;
		// position: relative;
		// top:-185px;
		
		.search-wrap{
			height: 100%;
			.list(column);
		}
			

			.search-form{
				height: 72px;
		
				padding:20px;
				display:-webkit-box;
				position: relative;
				background-color: @color-background;
				.search-input{
					position: relative;
					-webkit-box-flex: 1;
					.search-input-text{
						border-radius: 6px;background-color: @color-background-l;
						height:40px;
						border:none;
						padding: 0 24px 0 70px;
						width:100%;
						box-sizing: border-box;
						line-height: 72px;
						height:72px;
						font-size: @font-size-medium;
					}
				}
				.icon-sousuo{
					position: absolute;
					top:19px;
					left:19px;
					font-size: @font-size-large;
				}
				.icon-empty{
					position: absolute;
					font-size: @font-size-large;
					top:18px;right:24px;
				}
				.delete{

					line-height: 72px;
					padding: 0 20px;
					font-size: @font-size-medium;
				}
			}
			.list-panel{
				flex: 1;
				background-color: @color-background-l;
			}
		}

			</style>



