<template>
	<transition name="translate">
		<div class="music-detail" ref="music" @touchstart.stop="touch" @touchmove.stop="touch" @touchend.stop="touch">
				<header class="music-detail-header">
					<img src="" alt="" class="music-img">
					<div class="music-intr">
						<h4>想我这样的人</h4>
						<p>毛不易</p>
					</div>
					<i class="icon-play"></i>
				</header>
				<main class="lyric">
					<div class="wrap">

						<p></p>

					</div>
				</main>
				<footer class="music-control">
					<p class="colect">
						<i class="icon-xihuan"></i>
					</p>
					<div class="music-time">
						<span class="music-start">
							00:10
						</span>
						<div class="music-time-line">
							<div class="bg-line"></div>
							<div class="music-process"></div>
						</div>
						<span class="music-end">00:10</span>
					</div>
					<a href="" class="download">下载</a>

				</footer>
				<div class="bg-blur">
				</div>
				<div class="bg-mask"></div>

			</div>

		</transition>
	</template>

	<script>
	export default{ 
		name:"music-detail",
		mounted(){
			// this.$refs.music.style.height=window.innerHeight+"px";
		},
		methods:{
			touch(){console.log(1)}
		}

	}

	</script>

	<style lang="less" type="text/less" scoped>
	@import "~common/less/variable";
	@import "~common/less/mixin";
	.music-detail{
		position: fixed;

		left: 0;
		top: 0;
		right:0;
		bottom:0;
		z-index: 1001;
		color:@color-text-w;
		box-sizing: border-box;
		background-color: #000;
		display: flex;
		flex-flow: column nowrap;
		.music-detail-header{
			padding:30px;
			display: flex;
			flex-flow: row nowrap;
			padding-bottom:30px;
			height: 160px;
			background-color:rgba(0,0,0,.1);
			.music-img{
				display:block;
				width: 120px;
				margin-right:30px;
			}
			.music-intr{
				color:@color-text-l;
				font-size: @font-size-medium-x;
				white-space: nowrap;
				overflow:hidden;
				flex:1;
				height: 160px;
				display:flex;
				flex-flow: column nowrap;
				justify-content: center;
				h4{
					line-height: 60px;
				}
				p{

					font-size:@font-size-medium ;
				}

			}
			.icon-play{
				display:block;
				width:90px;
				font-size: 90px;
				color:@color-text-l;
				line-height: 160px;
			}

		}
		.lyric{
			flex:1;
			position:relative;
			background-color: #fff;
			.wrap{
				text-align: center;
				p{
					color:rgba(255,255,255,.6);
					font-size: @font-size-medium-x;
					.no-wrap;
					line-height: 84px; 
					&.active{
						color:@color-text-green;
					}
				}
			}


		}
		.music-control{
			height: 308px;
			.colect{
				text-align: right;
				.icon-xihuan{
					margin-right:30px;
					font-size: 80px;
				}

			}
			.music-time{
				display: flex;
				flex-flow: row nowrap;
				height: 80x;
				.music-start,.music-end{
					font-size: @font-size-small;
					display: block;
					width: 110px;
					line-height: 80px;
					text-align: center;
				}
				.music-time-line{
					flex:1;
					position: relative;
					.bg-line,.music-process{
						width: 100%;
						height: 2px;
						position: absolute;
						top:39px;
						left: 0;
						background-color: rgba(0,0,0,.1);

					}
					.music-process{
						width: 0;
						background-color:@color-text-green;
					}
				}
			}
		}
		.download{
				// line-height: 80px;
				margin:0 auto;
				display: block;
				text-align: center;
				background-color:@color-theme;
				color:@color-text-l;
				width: 50%;
				border-radius: 50px;
				line-height: 80px;
				font-size: @font-size-large-s;
			}
		}
		.translate-enter-active,.translate-fade-active{

			transition:all 1s
		}
		.translate-enter,.translate-leave-to{
			transform: translateX(100%);
		}

		</style>


